<template>
  <div :id="'tab-'+id" v-show="isActive">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    id:{
      required: false
    },
    name: {
      required: true,
    },
    selected: {
      default: false,
    },
  },
  data() {
    return {
      isActive: false,
    };
  },
  computed: {
    href() {
      return "#" + this.name.toLowerCase().replace(/ /g, "-");
    },
  },
  mounted() {
    this.isActive = this.selected;
  },
};
</script>

